@import "../variables.scss";

.popup {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.3);
}

.popup--showing {
  display: block;
  position: fixed;
}

.popup--closed {
  display: none;
  position: relative;
}

.popup__form {
  position: absolute;
  top: calc(50% - 302px);
  left: calc(50% - 245px);
  box-shadow: 0 5px 10px 0 rgba(0, 1, 1, 0.25);
}

@media (max-width: $mobile-width-only) {
  .popup__form {
    left: 0;
  }
}